.section-title {
  margin: 0;
  margin-bottom: 32px;
  padding: 0;
  color: $color-nero;
  font-size: 40px;
  font-style: normal;
  font-weight: 400;
  line-height: 48px;
  text-align: center;

  @include viewport--sm {
    font-size: 32px;
    line-height: 40px;
    text-align: left;
  }

  &--left {
    text-align: left;
  }
}
